/* Container geral da seção */
.guia-FERA-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  font-family: 'Arial', sans-serif;
  margin-top: 0;
}

/* Título da seção */
.titulo-guia-FERA h1 {
  font: var(--title-paragraph-font);
  text-align: center;
  margin-bottom: 1.5rem;
  color: #000;
  text-decoration: underline;
  text-underline-offset: 8px;
  letter-spacing: 2px;
}

.destaque-FERA {
  color: #00065e; 
}


.descricao-guia-FERA {
  display: flex;
  justify-content: center;
}

/* Texto de descrição abaixo do título */
.descricao-guia-FERA p {
  font-size: var(--paragraph-font-size);
  text-align: center;
  max-width: 80%;
  color: #555;
  font-family: "Lato", sans-serif;
  letter-spacing: 1px;

}

/* Bloco dos "Primeiros Passos" */
.box-primeirospassos-guia-FERA {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Título dos passos */
.titulo-primeiros-passos {
  font: var(--title-paragraph-font);
  margin-bottom: 3rem;
  color: #000000;
  letter-spacing: 2px;
}

/* Área que contém os 3 cards lado a lado */
.cards-passos {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

/* Cada card individual */
.card-passo {
  color: rgb(2, 2, 2);
  background-color: #fff;
  box-shadow: 0 5px 8px rgba(0,0,0,0.2);
  padding: 1.5rem;
  border-radius: 15px;
  width: 30%;
  text-align: center;
}

/* Ícone no topo do card */
.icone-card {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(255, 255, 255);
  font-size: var(--paragraph-font-size);
  margin-bottom: 0.5rem;
  background: #000fda;
  width: 50%;
  height: 40px;
  padding: 4px;
  margin: -3rem auto 0 auto;
  border-radius: 10px;
}

/* Título do card */
.titulo-card {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  letter-spacing: 1px;
}

/* Texto do card */
.texto-card {
  font: var(--paragraph-font);
  line-height: 1.3;
    letter-spacing: 1px;
    text-align: justify;
}


@media (max-width: 762px) {
  .cards-passos {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3rem;
  }

  .card-passo {
    width: 100%;
  }
}
