.secao-tutor{
    width: 80%;
    height: 100%;
    margin: 0px auto;
    display: flex;
}

.parteEsquerda-tutor-container{
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.nome-tutor-container{
    width: 100%;
    margin: 0px auto;
    margin-top: 2rem;
    margin-bottom: 0rem;
    height: 10%;
}

.nome-tutor {
    font: var(--title-paragraph-font);
    font-weight: 00;
    color: #1f1f1f;
    text-align: center;
    letter-spacing: 2px;
}

.descricao-tutor-container{
    width: 90%;
    margin: 0px auto;
    height: 65%;
    padding: 5px;
}

.descricao-tutor {
  font: var(--paragraph-font);
  line-height: 1.6;
  text-align: justify;
  color: #333;
}

.parteDireita-tutor-container{
    width: 30%;
    align-items: center;
    justify-content: center;
}

.fotoTutor-container {
    width: 85%;
    margin: 0 auto; /* Centraliza o container principal */
    height: 75%;
    margin-top: 0.5rem;

}

.fotoTutor {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.foto-tutor-image {
    width: 80%;
    height: 100%;
    object-fit: cover;
}

.hiperlinkTutor-container{
    width: 85%;
    margin: 0px auto;
    margin-top: 0.5rem;
    height: 10%;
}

.texto-hiperlink-professora{
    font: var(--other-font);
    color: #1f1f1f;
    text-align: center;
    letter-spacing: 1px;
}

.link-professora{
    font: var(--other-font);
    color: #274c9eff;
    text-align: center;
    letter-spacing: 1px;
    font-weight: 600;
}

.link-professora:hover {
    color: rgb(73, 131, 255);
}

.destaque-titulo {
    font: var(--paragraph-font);
    font-weight: bold;
    color: #000661; /* Azul PET */
    text-transform: uppercase;
    letter-spacing: 1px;
}

/* Media Query para Monitores Grandes (1440px+) */
@media (min-width: 1440px) {
    .secao-tutor {
        width: 70%;
        height: auto;
    }
    
    .nome-tutor-container {
        margin-top: 3rem;
        height: 15%;
    }
    
    .nome-tutor {
        letter-spacing: 3px;
    }
    
    .descricao-tutor-container {
        padding: 15px;
    }
    
    .descricao-tutor {
        line-height: 1.8;
    }
    
    .fotoTutor-container {
        margin-top: 1rem;
    }
    
    .foto-tutor-image {
        width: 70%;
    }
    
    .hiperlinkTutor-container {
        margin-top: 1rem;
    }
    
    .destaque-titulo {
        letter-spacing: 2px;
    }
}

/* Media Query para Tablets (768px - 1024px) */
@media (min-width: 768px) and (max-width: 1024px) {
    .secao-tutor {
        width: 100%;
        flex-direction: column;
        height: auto;
        align-items: center;
        justify-content: center;
    }

    .parteDireita-tutor-container {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    
    .nome-tutor {
        letter-spacing: 1.5px;
    }
    
    .descricao-tutor-container {
        width: 95%;
        height: auto;
        padding: 10px 0;
    }
    
    .descricao-tutor {
        display: none; /* Esconde o texto */
    }
    
    .fotoTutor-container {
        width: 40%;
        margin-top: 1rem;
    }
    
    .hiperlinkTutor-container {
        width: 90%;
        margin-top: 1rem;
        height: auto;
        padding-bottom: 1.5rem;
    }
    
    .texto-hiperlink-professora{
        letter-spacing: 1px;
    }
}

/* Estilos para telas menores que 768px (mobile) */
@media (max-width: 768px) {

  .secao-tutor {
    width: 90%;
    flex-direction: column; /* Empilha os elementos verticalmente */
    align-items: center;
  }

  .parteEsquerda-tutor-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .descricao-tutor-container {
    display: none; /* Esconde o texto */
  }

  .parteDireita-tutor-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .fotoTutor-container {
    width: 60%;
    height: auto;
    margin-top: 0rem;
  }

  .fotoTutor {
    width: 100%;
    height: auto;
  }

  .foto-tutor-image {
    width: 100%;
    height: auto;
    object-fit: cover;
  }

  .hiperlinkTutor-container {
    width: 100%;
    margin-top: 0rem;
  }

    .texto-hiperlink-professora{
        letter-spacing: 1px;
    }

    .link-professora {
        display: block;
        text-align: center;
    }
}
